



$(document).ready(function () {
    $('a[href*="#"]').bind('click', function (e) {
        e.preventDefault(); // prevent hard jump, the default behavior

        var target = $(this).attr("href"); // Set the target as variable

        // perform animated scrolling by getting top-position of target-element and set it as scroll target
        $('html, body').stop().animate({

            scrollTop: $(target).offset().top
        }, 300, function () {
            location.hash = target; //attach the hash (#jumptarget) to the pageurl
        });

        return false;
    })
        
    initSlider();
    
    
    $('#menu .icon-mobile').click(function () {
        if ($(this).closest('#menu').find('.right').hasClass('active')) {
            $(this).closest('#menu').find('.right').removeClass('active')
        } else {
            $(this).closest('#menu').find('.right').addClass('active')
        }
    })

    function goToByScroll(echo) {
        // Remove "link" from the ID
        // echo = echo.replace("link", "");
        $('html,body').animate({
            scrollTop: $("." + echo).offset().top
        }, 'slow');
    }


    

    $("#menu > ul > li > a").click(function(e) {
        // Prevent a page reload when a link is pressed
        e.preventDefault();
        // console.log($(this).attr('link'))
        // Call the scroll function
        goToByScroll( $(this).attr('link'));
    });


});

$(window).on('scroll',function() {
    var scrollDistance = $(window).scrollTop();

    // Show/hide menu on scroll
    //if (scrollDistance >= 850) {
    //		$('nav').fadeIn("fast");
    //} else {
    //		$('nav').fadeOut("fast");
    //}

    // Assign active class to nav links while scolling
    
    // var wd_width =  console.log($(window).width());
    // if(wd_width > 991){
    //     var al = $('.section');
    //     console.log(1)
    // }else{
    //     var al = $('.sct-mobile');
    //     console.log(2)
    // }
    
    $('.section').each(function(i) {
        if ($(this).position().top <= scrollDistance + 200) {
                $('.right li.active').removeClass('active');
                $('.right li').eq(i).addClass('active');
        }
    });
}).scroll();

function initSlider() {
    $('.slider').slick({
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        dots: false,
        arrows: false,
        autoplay: true
    });
}



